import React, { useEffect, useState } from "react";
import { Col, Row, Button } from "react-bootstrap";

import videos from "../videos/video_interior.mp4";
import flecha from "../images/iconos/btn_siguiente.svg";
import flechaRegreso from "../images/iconos/btn_atras.svg";

export default function VideoSeccion (props){

    const { refForm, none } = props;
    //console.log("Estamos en el videoSeccion, tengo ref: ", refForm)
    const [ ocultador, setOcultador ] = useState('flex');
    const [ colOcultador, setColOcultador ] = useState(8);

    useEffect(() => {
        if(none === "none"){
            setOcultador('none');
            setColOcultador(12);
        } else {
            setColOcultador(8);
            setOcultador('flex');
        }
    }, [colOcultador])

    return(
        <>
            <Row style={{ height: '14vh', paddingTop: 5}}>
                <Col lg={colOcultador} className="colVideoSeccion">              
                    <center>
                        <video  id="video_interior" autoPlay muted loop>
                        <source src={videos} type="video/mp4" />
                        </video>
                    </center>
                </Col>
                <Col lg={4} className={none === 'none' ? "colVideoSeccionOcultado" : "colVideoSeccion"}>
                    <Button onClick={() => refForm.current.click()} style={{width: '85%', height: '55%', borderRadius: 25}}>
                        <Row>
                            <Col lg={7} style={{display: 'flex', justifyContent: 'end', alignItems: 'center', fontSize: '2vh'}}>Avanzar</Col>
                            <Col lg={5} style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}><img className="btn-imagenSiguiente" src={flecha}/></Col>
                        </Row>
                    </Button>
                </Col>
            </Row>
        </>
    )

}